    justify-content: center;
    flex-direction: column;
    border-radius: 3rem;
    transition: all .3s linear; }
    .menu__icon:hover {
      background: #3273dc; }
      .menu__icon:hover > .one {
        top: -.6rem; }
      .menu__icon:hover > .three {
        top: .6rem; }
    .menu__icon div {
      width: 50%;
