      padding-right: 2rem;
      align-items: center; }
      .menu__multi--row:hover {
        background: #393e46 !important; }
      .menu__multi--row svg {
        transition: all .5s; }
        .menu__multi--row svg path {
          fill: #fff !important; }
    .menu__multi--button {
      padding: 1rem 2rem;
